<template>
    <div class="assistant-prompts">
        <div
            class="assistant-prompt-item"
            :class="`assistant-prompt-item-${prompts.length}`"
            v-for="(prompt, index) in prompts"
            :key="index"
            @click.stop="() => useHandle!(prompt)"
        >
            <a-tooltip :title="prompt">
                <div class="assistant-prompt-item-text">
                    {{ prompt }}
                </div>
            </a-tooltip>
        </div>
    </div>
</template>
<script setup lang="ts">
import { PropType, computed } from 'vue';

const props = defineProps({
    prompts: Array as PropType<string[]>,
    useHandle: Function as PropType<(text?: string) => void>,
});

const prompts = computed<string[]>(() => {
    return (props.prompts || []).slice(0, 3);
});
</script>

