<template>
    <div
        class="ai-assistans-card"
        :class="!data.userUsable && 'ai-assistans-card-disabled'"
        @click="() => useHandle()"
    >
        <div class="ai-assistans-card-header">
            <div class="ai-assistans-card-avatar">
                <AssistantAvarat
                    :icon="data.agentIcon"
                    :iconBackground="data.iconBackground"
                />
            </div>

            <div class="ai-assistans-card-header-content">
                <div class="ai-assistans-card-header-title">
                    <a-tooltip :title="data.agentName">
                        {{ data.agentName }}
                    </a-tooltip>
                </div>
                <a-tooltip :title="data.agentDesc">
                    <div class="ai-assistans-card-header-note">
                        {{ data.agentDesc }}
                    </div>
                </a-tooltip>
            </div>
        </div>
        <div class="ai-assistans-card-tips">
            <Prompts :prompts="data.prompts" :useHandle="useHandle" />
        </div>
        <div class="ai-assistans-card-actions">
            <div
                class="ai-assistans-card-number"
                v-if="data.agentType !== 'personal'"
            >
                <div
                    class="ai-assistans-card-action-item"
                    :class="
                        userThumbsUp && 'ai-assistans-card-action-item-blue'
                    "
                    @click="(e) => thumbsUpHandle(e)"
                >
                    <div class="ai-assistans-card-action-item-icon" />
                    <span class="ai-assistans-card-action-item-number">
                        {{ getNumber(thumbsUp) }}
                    </span>
                </div>
                <div
                    v-if="data.agentCode !== 'super'"
                    class="ai-assistans-card-action-item"
                    :class="
                        userFavorites && 'ai-assistans-card-action-item-blue'
                    "
                    @click="(e) => favoriteHandle(e)"
                >
                    <div class="ai-assistans-card-action-item-icon favorite" />
                    <span class="ai-assistans-card-action-item-number">
                        {{ getNumber(favorites) }}
                    </span>
                </div>
                <div class="ai-assistans-card-action-hot-item">
                    <hot />
                    <span class="ai-assistans-card-action-item-number">
                        {{ getNumber(heat) }}
                    </span>
                </div>
            </div>
            <div v-else />
            <div class="ai-assistans-card-source">
                来源：{{
                    data.agentType !== 'personal' ? data.domainName : '个人助手'
                }}
            </div>
        </div>
    </div>
</template>
<script setup lang="ts">
import { PropType, computed, ref } from 'vue';
import { message } from '@hwork/ant-design-vue';
import hot from './icons/hot.vue';
import Prompts from '@/components/Prompts/index.vue';
import { useRouter } from 'vue-router';
import { doFavorite, doThumbsUp, doUse } from '@/api/market';
import { addChatGroup, setPrompt } from '@/hooks/useChatGroupList';
import AssistantAvarat from '@/components/AssistantAvarat/index.vue';

const router = useRouter();
const props = defineProps({
    assistant: Object as PropType<Record<string, any>>,
});

const thumbsUp = ref<number>(props.assistant?.thumbsUp || 0);
const favorites = ref<number>(props.assistant?.favorites || 0);
const heat = ref<number>(props.assistant?.heat || 0);
const userFavorites = ref<boolean>(props.assistant?.userFavorites || false);
const userThumbsUp = ref<boolean>(props.assistant?.userThumbsUp || false);

const thumbsUpHandle = (e: Event) => {
    e.stopPropagation();
    if (!data.value.userUsable) {
        return;
    }

    userThumbsUp.value = !userThumbsUp.value;
    thumbsUp.value += userThumbsUp.value ? 1 : -1;
    doThumbsUp(props.assistant?.id, userThumbsUp.value);
};

const favoriteHandle = (e: Event) => {
    e.stopPropagation();
    if (!data.value.userUsable) {
        return;
    }

    userFavorites.value = !userFavorites.value;
    favorites.value += userFavorites.value ? 1 : -1;
    doFavorite(props.assistant?.id, userFavorites.value);
};

const useHandle = async (prompt?: string) => {
    if (!data.value.userUsable) {
        message.error(
            '当前助手无权限，无法正常使用。可联系所属领域管理员添加权限后再使用。',
        );
        return;
    }
    doUse(props.assistant?.id);

    if (prompt) {
        setPrompt(prompt);
        addChatGroup(
            props.assistant?.agentName,
            props.assistant?.agentCode,
            props.assistant?.agentIcon,
            props.assistant?.iconBackground,
            prompt,
        );
    } else {
        router.push({
            path: '/chat/new',
            query: {
                agent: props.assistant?.agentCode,
            },
        });
    }

    // 跳转到聊天
    // router.push({
    //     path: `/chat/${chatGroup.groupCode}`,
    //     query: {
    //         agent: props.assistant?.groupType,
    //     },
    // });
};

const getNumber = (num: number) => {
    return num > 999 ? '999+' : num;
};

const data = computed<Record<string, any>>(() => {
    return props.assistant!;
});
</script>
<style lang="less" scoped>
@import url('./index.less');
</style>
