<template>
    <div class="market-wrapper">
        <Header />
        <div class="market-search-wrapper">
            <Search v-model="searchText" />
            <Create ref="createRef" @refresh="refreshList" />
        </div>
        <Categories :change-handle="changeDomainList" />
        <div class="assistant-market-assistants">
            <div
                class="assistant-market-recent-use"
                v-if="!domains.length && recentUseAssistants.length"
            >
                <div class="assistant-market-subtitle">最近使用</div>
                <div class="assistant-market-recent-use-items">
                    <a-tooltip
                        v-for="assistant in recentUseAssistants"
                        :key="assistant.agentCode"
                        :title="assistant.agentDesc"
                    >
                        <div
                            class="assistant-market-recent-use-items-item"
                            @click="() => useHandle(assistant)"
                        >
                            <AssistantAvarat
                                :icon="assistant.agentIcon"
                                :iconBackground="assistant.iconBackground"
                                size="large"
                            />
                            <div class="assistant-name-text">
                                {{ assistant.agentName }}
                            </div>
                        </div>
                    </a-tooltip>
                </div>
            </div>
            <div class="assistant-market-filter">
                <div v-if="!domains.length" class="assistant-market-subtitle">
                    全部助手
                </div>
                <Segmented
                    v-if="showMyFilter"
                    :options="TYPES"
                    v-model:value="type"
                />
                <div v-else />
                <Sort
                    v-if="!showSelf"
                    :sort="sort"
                    :changeHandle="changeSortHandle"
                />
            </div>

            <div
                class="assistant-market-default-assistants"
                ref="assistantsRef"
            >
                <a-spin :spinning="loading">
                    <a-row :gutter="[12, 12]" v-if="assistants.length">
                        <a-col
                            :span="spanNumber"
                            v-for="assistant in assistants"
                            :key="assistant.id"
                        >
                            <AssistantSelfCard
                                v-if="showSelf"
                                :assistant="assistant"
                                @delete="refreshList"
                                @edit="editAssistant"
                            />
                            <AssistantCard v-else :assistant="assistant" />
                        </a-col>
                    </a-row>
                    <div class="assistant-market-empty-data" v-else>
                        <img :src="emptyDataImage" />
                        <span>暂无数据</span>
                    </div>
                </a-spin>
            </div>
        </div>
    </div>
</template>
<script setup lang="ts">
import { ref, watch, computed } from 'vue';
import Header from './Header/index.vue';
import Search from './Search/index.vue';
import Create from './CreateAssistant/index.vue';
import Categories from '../Categories/index.vue';
import Segmented from '@/components/Segmented';
import AssistantCard from '@/components/AssistantCard/index.vue';
import AssistantSelfCard from '@/components/AssistantSelfCard/index.vue';
import AssistantAvarat from '@/components/AssistantAvarat/index.vue';
import Sort from '../Sort/index.vue';
import { TYPES } from '../constants';
import {
    getMarketList,
    getMarketRecentUseList,
    doUse,
    AgentType,
} from '@/api/market';
import emptyDataImage from '@/assets/images/empty-data.png';
import { onMounted } from 'vue';
import { message } from '@hwork/ant-design-vue';
import { useRouter } from 'vue-router';

const searchText = ref<string>('');
const type = ref<string>('我的使用');
const domains = ref<string[]>([]);
const assistants = ref<Record<string, any>[]>([]);
const sort = ref<string>('HEAT_DESC');
const loading = ref<boolean>(true);
const spanNumber = ref<number>(12);
const assistantsRef = ref<HTMLDivElement>();
const createRef = ref<InstanceType<typeof Create>>();
const recentUseAssistants = ref<AgentType[]>([]);
const router = useRouter();

onMounted(() => {
    // 获取最近使用的助手列表
    getMarketRecentUseList().then((response) => {
        recentUseAssistants.value = response.data as any;
    });
    if (assistantsRef.value) {
        if (assistantsRef.value.getBoundingClientRect().width < 730) {
            spanNumber.value = 12;
        } else {
            spanNumber.value = 8;
        }
    }
});

const changeDomainList = (domain: string) => {
    if (domain === 'all') {
        domains.value = [];
    } else {
        domains.value = [domain];
    }
};

const useHandle = async (target: AgentType) => {
    if (!target.userUsable) {
        message.error(
            '当前助手无权限，无法正常使用。可联系所属领域管理员添加权限后再使用。',
        );
        return;
    }
    doUse(target.id);
    router.push({
        path: '/chat/new',
        query: {
            agent: target.agentCode,
        },
    });
};

const changeSortHandle = (target: string) => {
    sort.value = target;
};
const editAssistant = (code: string) => {
    createRef.value?.show(code);
};
const refreshList = () => {
    type.value = '个人助手';
    domains.value = ['my'];
};

const showMyFilter = computed(() => {
    if (Array.isArray(domains.value) && domains.value.length) {
        return domains.value[0] === 'my';
    } else {
        return false;
    }
});
const showSelf = computed(() => {
    return type.value === '个人助手' && domains.value[0] === 'my';
});

watch(
    () => [domains.value, searchText.value, type.value, sort.value],
    () => {
        loading.value = true;
        getMarketList({
            pageNumber: 1,
            pageSize: 50,
            domainList: domains.value,
            agentName: searchText.value,
            type: type.value,
            orderBy: sort.value,
        })
            .then((response) => {
                assistants.value = response.data.items;
            })
            .finally(() => {
                loading.value = false;
            });
    },
    {
        immediate: true,
        deep: true,
    },
);
</script>

<style scoped lang="less">
@import url('./index.less');
</style>
