import type { PropType } from 'vue';
import { defineComponent } from 'vue';
import SegmentedItem from './SegmentedItem';
import './index.less';

interface IOptionType {
    label?: string;
    value: string | number;
    disabled?: boolean;
}

export default defineComponent({
    name: 'Segmented',
    props: {
        value: {
            type: [String, Number],
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        options: {
            type: Array as PropType<IOptionType[]>,
            default: () => [],
        },
    },
    emits: ['change', 'update:value'],
    setup(props, { emit }) {
        const chooseItem = (option: IOptionType) => {
            if (!props.disabled && !option?.disabled) {
                emit('update:value', option.value);
                emit('change', option.value, option);
            }
        };
        return () => {
            const { value, disabled, options } = props || {};
            return (
                <div class={['segmented', disabled && 'segmented-disabled']}>
                    {options.map((option) => (
                        <SegmentedItem
                            {...option}
                            actived={option?.value === value}
                            disabled={disabled || option?.disabled}
                            onChoose={() => chooseItem(option)}
                        />
                    ))}
                </div>
            );
        };
    },
});
