<script setup lang="ts">
import { ref, watch, defineProps, PropType } from 'vue';
const props = defineProps({
    changeHandle: Function,
    sort: {
        type: String as PropType<string>,
        default: 'HEAT_DESC',
    },
});
const status = ref<string>(props.sort || 'HEAT_ASC');

const handleSort = () => {
    status.value = status.value === 'HEAT_DESC' ? 'HEAT_ASC' : 'HEAT_DESC';
};

watch(
    () => status.value,
    () => {
        if (props.changeHandle) props.changeHandle(status.value);
    },
);
