<template>
    <hot :changeHandle="changeHandle" :sort="sort" />
</template>
<script setup lang="ts">
import { PropType } from 'vue';
import hot from './icons/sort.vue';
const props = defineProps({
    sort: {
        type: String as PropType<string>,
        default: 'HEAT_DESC',
    },
    changeHandle: Function,
});

const changeHandle = (v: string) => {
    if (props.changeHandle) props.changeHandle(v);
};
</script>
