<template>
    <a-drawer
        :title="title"
        :visible="visible"
        :width="480"
        :getContainer="getContainer"
        @close="onClose"
    >
        <div class="form-wrap">
            <Avatar ref="avatarRef" :avatar="avatar" :avatarBg="avatarBg" />
            <div class="tips">
                <img src="@/assets/icons/icon-gpt.svg" />
                <div>共同探索安全合规的大模型服务</div>
            </div>
        </div>
        <a-form
            ref="formRef"
            layout="vertical"
            :model="formState"
            :rules="rules"
        >
            <a-form-item label="助手名称" name="name">
                <a-input
                    v-model:value="formState.name"
                    placeholder="请输入名称"
                    show-count
                    :maxlength="30"
                />
            </a-form-item>
            <a-form-item label="助手简介" name="description">
                <a-textarea
                    v-model:value="formState.description"
                    placeholder="AI助手的简单介绍，仅用于展示"
                    show-count
                    :maxlength="100"
                    :rows="4"
                />
            </a-form-item>
            <a-form-item label="助手设定" name="prompts">
                <a-textarea
                    v-model:value="formState.prompts"
                    placeholder="描述助手的设定信息（提示词），示例：你是专业的市场营销顾问，拥有丰富的行业经验和敏锐的市场洞察力。能够精准分析市场趋势，为我提供创新且有效的营销策略"
                    show-count
                    :maxlength="1000"
                    :rows="7"
                />
            </a-form-item>
        </a-form>
        <template #footer>
            <div class="btn-wrap">
                <a-button @click="onClose">取消</a-button>
                <a-button class="btn" type="primary" @click="onConfirm">
                    {{ confirmText }}
                </a-button>
            </div>
        </template>
    </a-drawer>
</template>
<script setup lang="ts">
import { ref, reactive } from 'vue';
import { useRouter } from 'vue-router';
import { message } from '@hwork/ant-design-vue';
import type { FormInstance } from '@hwork/ant-design-vue';
import { getContainer } from '@/utils/util';
import Avatar from './Avatar.vue';
import {
    createAssistant,
    AssistantType,
    assistantDetail,
    editAssistant,
} from '@/api/market';
const router = useRouter();
const visible = ref(false);
const title = ref('创建AI助手');
const confirmText = ref('创建');
const avatarRef = ref<InstanceType<typeof Avatar>>();
const formRef = ref<FormInstance>();
const formState = reactive<Record<string, string>>({
    name: '',
    description: '',
    prompts: '',
});
const agentCode = ref<string | undefined>(undefined);
const avatar = ref('');
const avatarBg = ref('');
const emit = defineEmits<{
    (e: 'refresh'): void;
}>();
const rules = {
    name: [{ required: true, message: '请输入助手名称' }],
    description: [{ required: true, message: '请输入助手简介' }],
    prompts: [{ required: true, message: '请输入助手设定' }],
};
const onClose = () => {
    visible.value = false;
    formRef.value?.resetFields();
    Object.keys(formState).map((key: string) => {
        formState[key] = '';
    });
    title.value = '创建AI助手';
    confirmText.value = '创建';
    avatar.value = '';
    avatarBg.value = '';
};
const onConfirm = async () => {
    try {
        const values = await formRef.value?.validateFields();
        const avatarData = avatarRef.value?.getAvatarData();
        const handler =
            confirmText.value === '创建' ? createAssistant : editAssistant;
        const code = confirmText.value === '创建' ? undefined : agentCode.value;
        handler({
            ...values,
            ...avatarData,
            code
        } as AssistantType).then((res) => {
            if (res.code === 10000) {
                message.success(`${confirmText.value}成功`);
                if (confirmText.value === '创建') {
                    router.push({
                        path: '/chat/new',
                        query: {
                            agent: res.data?.code,
                        },
                    });
                } else {
                    emit('refresh');
                }
                onClose();
            }
        });
    } catch (error) {
        console.log(error, '<=== error ===>');
    }
};
const show = async (code?: string) => {
    visible.value = true;
    if (code) {
        title.value = '编辑AI助手';
        confirmText.value = '保存';
        const res = await assistantDetail(code);
        formState.name = res.data.name;
        formState.description = res.data.description;
        formState.prompts = res.data.prompts;
        avatar.value = res.data.icon;
        avatarBg.value = res.data.iconBackground;
        agentCode.value = code;
    }
};
defineExpose({ show });
</script>

<style lang="less" scoped>
.form-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    .tips {
        display: flex;
        justify-content: center;
        color: #86909c;
        font-size: 12px;
        margin-top: 8px;
        img {
            margin-right: 4px;
        }
    }
}
.btn-wrap {
    display: flex;
    justify-content: flex-end;
    .btn {
        margin-left: 12px;
    }
}
</style>
