import { defineComponent } from 'vue';
import './index.less';

export default defineComponent({
    name: 'SegmentedItem',
    props: {
        value: {
            type: [String, Number],
        },
        label: {
            type: [String, Number],
        },
        tooltip: {
            type: String,
            default: '',
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        actived: {
            type: Boolean,
            default: false,
        },
    },
    emits: ['choose', 'active'],
    setup(props, { emit }) {
        const chooseItem = () => {
            if (!props.disabled) {
                emit('choose', props.value);
            }
        };
        return () => {
            const { label, value, tooltip } = props;
            return (
                <div
                    class={[
                        'segmented-item',
                        props.disabled && 'segmented-item-disabled',
                        props.actived && 'segmented-item-active',
                    ]}
                    onClick={chooseItem}>
                    <>
                        {tooltip ? (
                            <a-tooltip title={tooltip}>
                                {label || value}
                            </a-tooltip>
                        ) : (
                            label || value
                        )}
                    </>
                </div>
            );
        };
    },
});
