<template>
    <a-button type="primary" @click="show()" v-if="createBtnVisible">
        <template #icon>
            <h-icon-plus />
        </template>
        创建智能体
    </a-button>
    <Drawer ref="drawerRef" @refresh="$emit('refresh')" />
</template>
<script setup lang="ts">
import '@hwork/icon/search';
import { onMounted, ref } from 'vue';
import Drawer from './Drawer.vue';
import { checkPermission } from '@/api/permission';
const drawerRef = ref<InstanceType<typeof Drawer>>();
const show = (code?: string) => {
    drawerRef.value?.show(code);
};
const createBtnVisible = ref<boolean>(false);

onMounted(async () => {
    checkPermission().then((res) => {
        createBtnVisible.value = res?.data?.withinPurview;
    });
});

defineExpose({ show });
</script>
